body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.people {
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  justify-content: center;
  gap: 40px 30px;
  padding: 30px 35px;
  display: grid;
}

.people__person {
  position: relative;
}

@media screen and (width <= 499px) {
  .people {
    grid-template-columns: repeat(2, 200px);
    min-width: 500px;
  }
}

@media screen and (width >= 500px) {
  .people {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  color: #000;
  cursor: pointer;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 200px;
  padding: 10px 10px 9px;
  transition: transform 1s;
  display: flex;
  overflow: hidden;
}

.person__photo {
  position: relative;
}

.person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 12px;
  max-width: 264px;
  transition: all 1s;
  position: relative;
  transform: scale(1.23);
}

.photo__img {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  transition: all 2s;
  display: block;
}

.photo_rank {
  position: absolute;
}

.photo__rank {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.person:hover .photo__img {
  border-radius: 60px;
  transition: all 1s;
  transform: scale(1.03);
}

.person:not(:hover) .photo__img {
  transition: all 2s;
}

.rank {
  color: #8b0000;
  text-align: center;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  padding: 4px;
  line-height: 32px;
}

.details {
  color: #000;
}

.name {
  color: #2f4f4f;
  cursor: pointer;
  justify-content: center;
  padding: 9.6px 0 10.8px;
  font-size: 18px;
  font-weight: 600;
  line-height: 23px;
  text-decoration: none;
  display: flex;
}

.extra {
  color: #000;
  opacity: 0;
  justify-content: center;
  font-weight: 500;
  transition: all 1s;
  display: flex;
}

.person:hover .extra {
  opacity: 1;
  transition: all 1s;
}

.sport, .pay {
  color: #000;
  font-size: 14px;
}
